<template>
  <div class="my-order">
    <van-nav-bar
      title="充值明细"
      left-arrow
      @click-left="goToMemberCenter"
    />

    <div class="div-content">
      <div class="div-card-box" v-for="item in dataList" :key="item.id">
        <div class="div-card-box-title-line">
          <span class="span-line-value">{{ item.date }}</span>
        </div>

        <div class="div-sales-item">
          <div class="div-product-box-line">
            <span class="span-line-label">充值</span>
            <span class="span-line-value">￥{{ item.amount }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 引入 json 数据
import rawData from "@/utils/data.json";
export default {
  name: 'MyOrder',
  data() {
    return {
      dataList: [],
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      const phone = this.$route.query.phoneNumber
      if (phone) {
        const rechargeData = rawData.rechargeData || {}
        const dataList = rechargeData[phone] || []
        this.dataList = dataList
      } else {
        alert('请先登录')
        this.$router.push({
          name: 'LoginPage',
        })
      }
    },
    goToMemberCenter() {
      this.$router.push({
        name: 'MemberCenter',
        query: {...this.$route.query},
      })
    },
  },
}

</script>

<style scoped>
.my-order >>> .van-nav-bar {
  background-color: #977961;
}

.my-order >>> .van-nav-bar__title {
  color: #fff;
}

.my-order >>> .van-icon-arrow-left {
  color: #fff;
}

.div-content {
  padding: 10px;
  padding-top: 0;
}

.div-card-box {
  margin-top: 10px;
  border-radius: 8px;
  padding: 12px 24px;
  background-color: #fff;
}

.div-card-box-title-line {
  display: flex;
  justify-content: space-between;
  padding-bottom: 4px;
  border-bottom: 1px solid #ccc;
  font-size: 14px;
}

.span-line-label {
  color: #333;
}

.span-line-value {
  color: #999;
  font-weight: 400;
}

.div-sales-item {
  padding: 4px 0;
}

.div-product-box-line {
  display: flex;
  justify-content: space-between;
  padding: 2px 0;
  font-size: 14px;
  font-weight: 400;
}
</style>



